.FactionImg img{
    width: 90px;
    height: 80px;
    margin: 15px;
    opacity: 0.5;
}
.FactionImg img:hover{
    opacity: 1.5;
    width: 110px;
height: 100px;
}
.HeroSection{
    margin-left: 15px;
}

.Heros{
    width: 240px;
    height: 300px;
    margin: 20px;
    border-radius: 10%;
    border: dotted red;
    animation-name: scale-up-center;
    animation-duration: 2s;
    text-shadow: 2px 2px 4px #000000;
}
.container {
    float: left;
    position: relative;
    text-align: center;
    color: white;
    
  }
  .centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: red;
    font-size: 20px;
    display: none;
    z-index: 1;
    cursor: pointer;
  }
 
.container:hover .centered{
    display: inline;
   
    
}
.Heros:hover{
    opacity:0.5;
    cursor: pointer;
}
.Content{
    float: left;
    }

@-webkit-keyframes scale-up-center{0%{-webkit-transform:scale(.5);transform:scale(.5)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes scale-up-center{0%{-webkit-transform:scale(.5);transform:scale(.5)}100%{-webkit-transform:scale(1);transform:scale(1)}}